	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 15 – Black Eagles","texte":{"childMarkdownRemark":{"html":"<p>A little later in the evening, I watch the boys play football from my bedroom window smiling. Glad to know we're safe and well. I think about Tom and what happened to make him lose his life. Sam knocks on my door and looks at me.</p>\n<p>Sam: Wamien... I told you I'd explain what happened to Tom.<br>\nWamien: I was just thinking about him... I wish I could have saved him, but I was too late.<br>\nSam: It's not your fault, you know. It happened the day you ran away. The nurse came in to give him his sedative. He couldn't take it anymore. As soon as she walked into his room. He jumped her and suffocated her... I didn't think Tom was capable of such things... As he came out of his room two guards were facing him, having heard the sound that his assassination had produced. He collapsed in front of them, his heart had stopped beating, because he hadn't had his sedative in time.<br>\nWamien: I... I should have been there...</p>\n<p>Sam's coming to put his hand on my shoulder.</p>\n<p>Sam: You know, even I couldn't have done anything, it all happened right in front of my eyes. And then I was in front of him all day. He had not slept for almost 5 months, was exhausted and wanted to die.</p>\n<p>I'm speechless, but I know Sam's right, we all knew he only wanted one thing, to die.</p>\n<p>Sam: Look, now we're all safe here and everyone's fine.</p>\n<p>I smile for a moment.</p>\n<p>Wamien: I know, I know.</p>\n<p>Clara knocks on the door.</p>\n<p>Clara: Oh, am I bothering you boys?<br>\nWamien: It's okay.<br>\nSam: Yeah, I'm fine.<br>\nClara: Um, I just... I'm sorry about Tom. I didn't really know him, but he was a good person according to you.<br>\nWamien: I just wish I'd said goodbye before.<br>\nSam: But I do remember him.</p>\n<p>Sam took out the famous flash drive that he had stolen and that Darakei had taken back.</p>\n<p>Wamien: You know what's on that key.<br>\nSam: Nope, but you seem to know.<br>\nWamien: A version 2.0 of my armor, he had taken it for me and knew it concerned me. We'll keep it in his memory that he never dies.<br>\nClara: That's touching. Think you can apply version 2.0?<br>\nWamien: Without a doubt, all the data is there for that.<br>\nClara: Well, we'll see about that tomorrow. It's already 22:00.<br>\nSam: Oh come on! We're gonna party tonight.</p>\n<p>Clara smiled as she crossed her arms.</p>\n<p>Clara: All right, fine, but don't mess it up too much.<br>\nWamien: For my part, I'll stay calm in my room.<br>\nSam: As you want super savior.<br>\nWamien: Meh, I don't really like that nickname.<br>\nClara: Ahah, I know you like \"honey\" better as a nickname.<br>\nWamien: No more</p>\n<p>Clara laughs. Sam's getting out of my room. Clara slowly approaches me.</p>\n<p>Clara: You want to rest, I suppose?<br>\nWamien: Yeah.<br>\nClara: Just leave you a little something before you go to sleep.</p>\n<p>Clara approached and came to deposit a sweet kiss on my lips. She broke the kiss and came out of my room, gently closing the door. \"This woman, I think I'm in love.\" I hear them partying in the living room. I lie down in my bed and close my eyes. </p>\n<p>The next morning, I wake up, I feel heat against me. I slowly raise my head and see Clara sleeping against me. I'm blushing, but don't move. Clara wakes up slowly, she opens her eyes and looks at me. She blushes and gets up.</p>\n<p>Clara: Oh, I'm so sorry. You were already asleep when I arrived, I couldn't sleep.<br>\nWamien: I see, you should have woken me.<br>\nClara: I know, but you shouldn't wake a sleeping man.</p>\n<p>She laughs and I get up.</p>\n<p>Wamien: I'm gonna go get something to eat.<br>\nClara: All right, honey.<br>\nWamien: Grrr, I don't like it when you call me that, you know?<br>\nClara: Yeah hihi.</p>\n<p>I smile and leave the room for lunch. Kameron is already up. </p>\n<p>Wamien: Hi Kameron.<br>\nKameron: Hey Wamien, how are you?<br>\nWamien: Good, didn't party too much yesterday?<br>\nKameron: Just enough and you? Not too much, party with Clara?<br>\nWamien: What? I was asleep, let's not be ridiculous.<br>\nKameron: Yeah yeah, hey, I did some careful research just to reassure you.<br>\nWamien: All right... Go ahead.<br>\nKameron: Yesterday you said we weren't the only ones with powers, were you?<br>\nWamien: Yes and?<br>\nKameron: Well I found places where people, who supposedly have powers, have committed crimes or thefts.<br>\nWamien: They basically abuse their powers.<br>\nKameron: Yeah, and if you ask me, we should stop them.<br>\nWamien: I agree, but not now, we should learn to become stronger first.<br>\nKameron: And Tim smarter ahah.<br>\nWamien: Very funny Kameron, very funny.<br>\nKameron: Roh stop it is just a joke. Anyway, the name of their band is Black Eagles.<br>\nWamien: Uh, okay. I'll do my research.<br>\nKameron: Perfect.</p>\n<p>He smiled and left the kitchen to go into the living room.</p>\n<p>I make myself a bowl of cereal and then I go back to my room. Clara had gone back to sleep. I sit at my table and eat in silence watching her from time to time. After eating, I take Clara's laptop, hack into the password and search the Internet for the famous band \"Black Eagles\". Bank robbery, extortion and murder. Many events have happened on their behalf, which is not reassuring. One way to recognize them is that they wear an eagle patch in full flight on their left shoulder. That's good to know. </p>"}},"chapitreAvant":"chapter-14-presentations","chapitreApres":"chapter-16-tracking","nomRoman":"the-first-cyborg-volume-1","slug":"chapter-15-black-eagles","codeChapitre":"H0001C016","equivalentUrl":"/histoires/chapitre/chapitre-15-black-eagles"}},"pathContext":{"slug":"chapter-15-black-eagles"}}

